import {BrowserModule} from '@angular/platform-browser';
import {NgModule} from '@angular/core';
import {FormsModule} from '@angular/forms';
import {RouterModule, Routes} from '@angular/router';


import {AppComponent} from './app.component';
import {ListProductComponent} from './list-product/list-product.component';
import {CurrentOrderComponent} from './current-order/current-order.component';
import {ListOrderComponent} from './list-order/list-order.component';
import {AdminProductGestionComponent} from './admin-product-gestion/admin-product-gestion.component';
import {ProduitManagerService} from "./produit-manager.service";
import {HttpClientModule} from "@angular/common/http";


const routes: Routes = [
  {path: 'administration', component: AdminProductGestionComponent},
  {path: 'gestion-produit', component: ListProductComponent}
];

@NgModule({
  declarations: [
    AppComponent,
    ListProductComponent,
    CurrentOrderComponent,
    ListOrderComponent,
    AdminProductGestionComponent
  ],
  imports: [
    BrowserModule,
    FormsModule,
    HttpClientModule,
    [RouterModule.forRoot(routes)]
  ],
  providers: [ProduitManagerService],
  bootstrap: [AppComponent]
})
export class AppModule {
}




